			display: -webkit-box;
			display: -ms-flexbox;
			display: flex;
			-webkit-box-flex: 1;
			-ms-flex-positive: 1;
			flex-grow: 1;
			overflow: hidden;
			&>div {
				white-space: nowrap;
				text-overflow: ellipsis;
				padding-right: 20px;
				overflow: hidden;
			}
		}
		.file-click {
			display: flex;
			flex-shrink: 0;
			font-size: 14px;
			cursor: pointer;
			margin: 0px 10px;
			&:hover {
				color: #3C98E8;
			}
			&.file-click-del:hover {
				color: #F01400;
			}
		}
	}
	
	.content-confirm-btn {
		line-height: 40px;
		width: 140px;
		text-align: center;
		&.btn-blue-border {
			line-height: 38px;
			margin-right: 20px;
		}
	} //  替换按钮
	.formgroup-file-item-btn {
		position: relative;
		overflow: hidden;
		cursor: pointer;
		font-size: 14px;
	}
	
	.formgroup-file-item-btn:hover {
		color: #3C98E8;
	}
	
	.formgroup-file-item-btn-inputfile {
		position: absolute;
		left: 0px;
		top: -10px;
		right: 0px;
		bottom: 0px;
		opacity: 0;
		cursor: pointer;
	}
	
	.form-editorbox {
		.form-input {
			width: 740px;
			height: 300px;
		}
	}
	
	.content-confirm {
		//width: 790px;
		margin-left: 198px;
	}
	
	@media (max-width: 1350px) {
		.form-editorbox .form-input {
			width: 490px;
		}
	} //发送对象列表样式
	.show-select-companyInfo {
		background-color: #E7F4FF;
		width: 100%;
		padding: 7px 10px;
		margin-bottom: 10px;
		.line_company_title {
			&>span {
				display: inline-block;
			}
			.icon {
				width: 18px;
				height: 18px;
				background: url('../../images/icon_company.png') no-repeat;
				background-size: 100%;
				position: relative;
				top: 3px;
			}
			.companyName {
				color: #35383A;
				font-size: 14px;
			}
			.deleteBtn {
				cursor: pointer;
				width: 16px;
				height: 16px;
				background: url('../../images/icon_delete.png') no-repeat;
				background-size: 100%;
				position: relative;
				top: 5px;
			}
		}
		.line_person_list_box {
			position: relative;
			.line_person_list {
				min-height: 23px;
			}
			.line_person_list_left {
				display: inline-block; // background-color: #3C98E8;
				width: 110px;
				float: left;
				&>span {
					font-size: 14px;
				}
				.icon {
					width: 18px;
					height: 18px;
					display: inline-block;
					background: url('../../images/icon_position.png') no-repeat;
					background-size: 100%;
					position: relative;
					top: 3px;
				}
				.position {
					color: #A0A4AA;
				}
			}
			.line_person_list_right {
				padding-top: 3px;
				display: inline-block;
				width: calc(100% - 130px);
				float: left; //  background-color: #3C98E8;
				&>span {
					font-size: 14px;
					color: #35383A;
					padding-right: 10px;
				}
			}
			.deleteBtn {
				cursor: pointer;
				width: 16px;
				height: 16px;
				background: url('../../images/icon_delete.png') no-repeat;
				background-size: 100%;
				position: relative;
				top: 5px;
			}
		}
	}
	/*改版新增样式*/
	
	.investPersonaInfoPublish {
		height: 650px;
		padding: 20px 30px;
	}
</style>


